.mg-timeline, .mg-timeline-inline {
  line-height: normal; 
  }  
.mg-timeline > ul {
  position: relative;
  padding-left: 3.6rem;
  list-style: none;
}
.mg-timeline > ul::before {
  display: inline-block;
  content: "";
  position: absolute;
  top: 0;
  left: 1.5rem;
  width: 1rem;
  height: 100%;
}
.mg-timeline > ul li {
  position: relative;
  counter-increment: list;
  margin-bottom: 0;
}
.mg-timeline > ul li:not(:last-child) {
  padding-bottom: 1rem;
}
.mg-timeline > ul li::before {
  display: inline-block;
  content: "";
  position: absolute;
  left: -3rem;
  height: 100%;
  width: 10px;
  border-left: 0.3rem solid $color-quaternary;
}
.mg-timeline > ul li:last-child::before {
  border-color: none;
}
.mg-timeline > ul li::after {
  content: "";
  display: inline-block;
  position: absolute;
  top: 0;
  left: -3.6rem;
  width: 1.5rem;
  height: 1.5rem;
  border: 0.3rem solid $color-quaternary;
  border-radius: 50%;
  background-color: $color-initial;
}
.mg-timeline > ul li.active::before {
  border-left: 0.3rem solid $color-primary;
}
.mg-timeline > ul li:last-of-type::before {
  border-left: 0.3rem solid transparent !important;
}

.mg-timeline > ul li.active::after {
  font-size: 1rem;
  text-align: center;
  border: 0.3rem solid $color-primary;
  background-color: $color-primary;
}
.mg-timeline > ul li.current::after {
  border: 0.3rem solid $color-primary;
  width: 1.5rem;
  height: 1.5rem;
  left: -3.6rem;
  border-radius: 50%;
}

.mg-timeline-inline > ul {
  list-style: none;
  display: flex;
  flex-direction: row;
}
.mg-timeline-inline > ul li {
  border-top: 0.3rem solid $color-quaternary;
  flex: 1 1 0px;
}

.mg-timeline-inline > ul li.active {
  border-color: $color-primary;
}
.mg-timeline-inline > ul li.active::before {
  font-size: 1rem;
  text-align: center;
  border-color: $color-primary;
  background-color: $color-primary;
}
.mg-timeline-inline > ul li.current::before {
  border: 0.3rem solid $color-primary;
  border-radius: 50%;
}
.mg-timeline-inline > ul li::before {
  content: "";
  position: absolute;
  left: 0;
  display: inline-block;
  position: relative;
  vertical-align: text-top;
  bottom:calc(1.5rem/2); 
  width: 1.5rem;
  height: 1.5rem;
  border: 0.3rem solid $color-quaternary;
  border-radius: 50%;
  background-color: $color-initial;
}
.mg-timeline-inline > ul li:last-of-type {
  border-top: 0.3rem solid transparent;
  flex: 0 1 auto;
}
